import React, { FC } from 'react';
import {
  boolColumn,
  CellEditProps,
  ClickEnum,
  ColumnsProps,
  DefaultSortProps,
  FilterType,
  getExtraData,
  OrderType,
  SearchDataGrid,
  SearchViewProps,
  WindowComponent,
} from 'summer';
import { CLASS_NAME, CodigoReceitaDetalhamento } from '../../../../models/dtos/mlf/codigoReceitaDetalhamento';

const defaultSorted: DefaultSortProps[] = [
  {
    dataField: 'descricao',
    order: OrderType.ASC,
  },
];

const cellEditProps: CellEditProps = {
  blurToSave: true,
  mode: ClickEnum.CLICK,
};

const CodigoReceitaFinanceiraDetalhamentoSearchView: FC<SearchViewProps<CodigoReceitaDetalhamento>> = props => {
  const columns: ColumnsProps[] = [
    {
      dataField: 'codigo',
      editable: true,
      filter: {
        type: FilterType.TEXT,
      },
      sort: true,
      text: 'Código',
    },
    {
      dataField: 'descricao',
      editable: true,
      filter: {
        type: FilterType.TEXT,
      },
      sort: true,
      text: 'Descrição',
    },
    boolColumn({
      dataField: 'instituicaoFinanceira',
      editable: false,
      filter: true,
      sort: true,
      text: 'Instituição Financeira',
    }),
    boolColumn({
      dataField: 'segurosPrivados',
      editable: false,
      filter: true,
      sort: true,
      text: 'Seguros Privados',
    }),
    boolColumn({
      dataField: 'previdenciaComplementar',
      editable: false,
      filter: true,
      sort: true,
      text: 'Previdência Complementar',
    }),
    boolColumn({
      dataField: 'capitalizacao',
      editable: true,
      filter: true,
      sort: true,
      text: 'Capitalização',
    }),
    boolColumn({
      dataField: 'assistenciaSaude',
      editable: false,
      filter: true,
      sort: true,
      text: 'Assistência de Saúde',
    }),
    boolColumn({
      dataField: 'multiplos',
      editable: false,
      filter: true,
      sort: true,
      text: 'Múltiplos',
    }),
  ];

  return (
    <WindowComponent title="Código de Detalhamento de Receita de Instituições Financeiras" wikiId={2378}>
      <SearchDataGrid
        {...props}
        {...props.searchDataGridProps}
        dtoClassName={CLASS_NAME}
        columns={columns}
        cellEditProps={cellEditProps}
        defaultSorted={defaultSorted}
        extraData={getExtraData(props, 'CodigoReceitaFinanceiraDetalhamentoEditView', CLASS_NAME)}
      />
    </WindowComponent>
  );
};

export default CodigoReceitaFinanceiraDetalhamentoSearchView;
